@import url("../style/_reset.scss");
.header {
  width: 100%;
  height: 5vw;
  // border: 1px solid green;
  position: fixed;
  z-index: 100;
  margin: 0 auto;
  // left: 5vw;
  line-height: 1vw;
  // background-color: #fff;
  transition: background-color 0.3s ease;
  .on {
    opacity: 1;
  }
  .off {
    opacity: 0;
  }
  // &.scrolled {
  //   background-color: rgba(
  //     255,
  //     255,
  //     255,
  //     0.9
  //   ); // 스크롤이 100 이상인 경우 배경색 변경
  // }
  ul {
    position: relative;
    display: flex;
    width: 50%;
    justify-content: space-between;
    top: 4.5vh;
    right: -44.5vw;
    font-size: 2.3vh;
    // border: 1px solid;
    text-align: center;
    li {
      width: 35%;
      height: 20%;
      height: 3.3vh;
      color: gray;

      // border: 1px solid;

      .svg-inline--fa {
        width: 30%;
        height: 100%;
        // border: 1px solid;

        cursor: pointer;

        &:hover {
          // border-bottom: 0.5vh solid yellow;
          color: black;
        }
      }
      // &:first-of-type {
      //   // 부모의 첫 번째 자식에 대한 스타일
      //   color: black;
      //   a {
      //     &:first-of-type {
      //       border-bottom: 5px solid transparent; /* 초기에는 투명한 선으로 설정 */
      //       transition: border-bottom 0.5s ease-in-out;
      //     }
      //     &.active {
      //       // border-bottom: 5px solid yellow;
      //     }
      //   }
      // }
      h2 {
        position: absolute;
        left: -95%;
        top: 10%;
        font-weight: 400;
        font-size: 2.3vw;
        color: black;
      }
    }
  }
  .title {
    position: absolute;
    font-size: 6vh;
    top: 50px;
    font-family: "Righteous", serif;
    left: 10%;
    transition: all 0.5s ease-in;
    .show {
      opacity: 1; /* titleRef가 변경되어야 보여집니다. */
    }
  }
}
